.homePage {
  background: -webkit-gradient(linear, left bottom, left top, from(#e6e9f0), to(#eef1f5));
  background: -webkit-linear-gradient(bottom, #e6e9f0 0%, #eef1f5 100%);
  background: -o-linear-gradient(bottom, #e6e9f0 0%, #eef1f5 100%);
  background: linear-gradient(to top, #e6e9f0 0%, #eef1f5 100%);
  text-align: center;
}

canvas {
  display: block;
  position: fixed;
  z-index: -10;
}

.headerLogo {
  width: 40%;
  margin: 30px 30%;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 0;
  background: #757f9a;
  background: -webkit-linear-gradient(
    to right,
    rgba(45, 112, 181, 1),
    rgba(83, 168, 220, 1)
  );
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(45, 112, 181, 1)),
    to(rgba(83, 168, 220, 1))
  );
  background: -webkit-linear-gradient(
    left,
    rgba(45, 112, 181, 1),
    rgba(83, 168, 220, 1)
  );
  background: -o-linear-gradient(
    left,
    rgba(45, 112, 181, 1),
    rgba(83, 168, 220, 1)
  );
  background: linear-gradient(
    to right,
    rgba(45, 112, 181, 1),
    rgba(83, 168, 220, 1)
  );
  background-position: center;
}

.features {
  margin: 0px;
}

