.pricing {
  padding-top: 3em auto;
  margin: 0 auto;
  background: #757f9a; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    rgba(45, 112, 181, 0.7),
    rgba(83, 168, 220, 0.7)
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(45, 112, 181, 0.7)),
    to(rgba(83, 168, 220, 0.7))
  );
  background: -webkit-linear-gradient(
    left,
    rgba(45, 112, 181, 0.7),
    rgba(83, 168, 220, 0.7)
  );
  background: -o-linear-gradient(
    left,
    rgba(45, 112, 181, 0.7),
    rgba(83, 168, 220, 0.7)
  );
  background: linear-gradient(
    to right,
    rgba(45, 112, 181, 0.7),
    rgba(83, 168, 220, 0.7)
  );
}

.card {
  text-align: center;
  width: 88%;
  margin: 0.44em auto;
  -webkit-box-shadow: 0 1px 5px rgba(0, 0, 0, 0.46);
          box-shadow: 0 1px 5px rgba(0, 0, 0, 0.46);
  background-color: white;
  background: -webkit-gradient(linear, left bottom, left top, from(#e6e9f0), to(#eef1f5));
  background: -webkit-linear-gradient(bottom, #e6e9f0 0%, #eef1f5 100%);
  background: -o-linear-gradient(bottom, #e6e9f0 0%, #eef1f5 100%);
  background: linear-gradient(to top, #e6e9f0 0%, #eef1f5 100%);
}

.comingSoon {
  background-color: rgba(0, 0, 0, 0.5);
  position: absolute;
  z-index: 2;
  width: 90%;
  height: 100%;
}

.cardButton {
  width: 100%;
  border: black;
  margin-bottom: 1em;
  -webkit-box-shadow: 0 1px 5px rgba(0, 0, 0, 0.46);
          box-shadow: 0 1px 5px rgba(0, 0, 0, 0.46);
  background: #757f9a; /* fallback for old browsers */
  background: -webkit-linear-gradient(
    to right,
    rgba(45, 112, 181, 0.7),
    rgba(83, 168, 220, 0.9)
  ); /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(45, 112, 181, 0.7)),
    to(rgba(83, 168, 220, 0.9))
  );
  background: -webkit-linear-gradient(
    left,
    rgba(45, 112, 181, 0.7),
    rgba(83, 168, 220, 0.9)
  );
  background: -o-linear-gradient(
    left,
    rgba(45, 112, 181, 0.7),
    rgba(83, 168, 220, 0.9)
  );
  background: linear-gradient(
    to right,
    rgba(45, 112, 181, 0.7),
    rgba(83, 168, 220, 0.9)
  );
}

.card-title {
  text-align: center;
  margin: 1.5em auto;
}

.card-body {
  text-align: left;
  padding: 0em 2em;
}

.card-list {
  margin-bottom: 2em;
}

