.signin-form-wrap {
  height: 100vh;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(45, 112, 181, 0.7)),
    to(rgba(83, 168, 220, 0.7))
  );
  background: -webkit-linear-gradient(
    left,
    rgba(45, 112, 181, 0.7),
    rgba(83, 168, 220, 0.7)
  );
  background: -o-linear-gradient(
    left,
    rgba(45, 112, 181, 0.7),
    rgba(83, 168, 220, 0.7)
  );
  background: linear-gradient(
    to right,
    rgba(45, 112, 181, 0.7),
    rgba(83, 168, 220, 0.7)
  );
}

.signin-form {
  width: 88%;
  padding: 4em 1em;
  -webkit-box-shadow: 0 1px 7px rgba(0, 0, 0, 0.46);
          box-shadow: 0 1px 7px rgba(0, 0, 0, 0.46);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  background: -webkit-gradient(linear, left bottom, left top, from(#e6e9f0), to(#eef1f5));
  background: -webkit-linear-gradient(bottom, #e6e9f0 0%, #eef1f5 100%);
  background: -o-linear-gradient(bottom, #e6e9f0 0%, #eef1f5 100%);
  background: linear-gradient(to top, #e6e9f0 0%, #eef1f5 100%);
}

.logo {
  width: 300px;
  margin-bottom: 2em;
}

.inputWidthTemp {
  width: 300px;
}

.signInButton {
  margin: 1em;
}



